import { useTranslation } from "react-i18next";
import { useTheme } from "styled-components";

export const useNpsAllCustomersChart = (percentageWhoSeeSurvey: number, npsSurveyUrl: string) => {
    const { t } = useTranslation("campaign", { keyPrefix: "seamless-survey.nps.step.triggering-conditions" });
    const { colors } = useTheme();
    const askForNPSCopy = String(t("nodes.origin-survey", "Ask for NPS score")).padEnd(40, "\u00A0\u00A0");

    return `
%%{init: {'flowchart' : {'curve' : 'bumpX'}}}%%
flowchart LR
    %% Define nodes
    PURCHASE_COMPLETE((${t("nodes.purchase-complete", "Purchase\nComplete")}))
    ALL_CUSTOMERS(${t("nodes.all-customers", "All Customers")})
    CUSTOMER_SELECTION{${t("nodes.customer-selection", "Customer\nSelection")}}
    PERC_SEE_SURVEY("${percentageWhoSeeSurvey}%")
    PERC_NOT_SEE_SURVEY("${100 - percentageWhoSeeSurvey}%")
    ASK_NPS_SURVEY[<div class="flow-chart-iframe-label">&nbsp;&nbsp;&nbsp;${askForNPSCopy}</div><iframe width="450" height="300" class="flow-chart-preview-iframe" src="${npsSurveyUrl}">]
    USUAL_MENTION_ME_FLOW(${t("nodes.usual-mention-me-flow", "To the usual Mention Me flow")})

    %% Define connections and their styles
    PURCHASE_COMPLETE-->ALL_CUSTOMERS
    linkStyle 0 stroke:${colors.MidnightBlue80Dark},stroke-width:4px

    ALL_CUSTOMERS-->CUSTOMER_SELECTION
    linkStyle 1 stroke:${colors.MidnightBlue80Dark},stroke-width:4px

    CUSTOMER_SELECTION-->PERC_SEE_SURVEY
    linkStyle 2 stroke:${colors.MidnightBlue80Dark},stroke-width:4px

    PERC_SEE_SURVEY-->ASK_NPS_SURVEY
    linkStyle 3 stroke:${colors.MidnightBlue80Dark},stroke-width:4px
    
    ASK_NPS_SURVEY-.->ONWARDS1
    linkStyle 4 stroke:${colors.Navy40},stroke-width:3px
    
    CUSTOMER_SELECTION-->PERC_NOT_SEE_SURVEY
    linkStyle 5 stroke:${colors.Navy40},stroke-width:3px

    PERC_NOT_SEE_SURVEY-->USUAL_MENTION_ME_FLOW
    linkStyle 6 stroke:${colors.Navy40},stroke-width:3px
    
    USUAL_MENTION_ME_FLOW-.->ONWARDS2
    linkStyle 7 stroke:${colors.Navy40},stroke-width:3px
    
    %% Define styles
    classDef green_circle font-family:roboto,fill:${colors.JadeGreen80Dark},stroke-width:0,color:${colors.White},font-size:12px,font-weight:400;
    classDef pathway font-family:roboto,stroke:${colors.MidnightBlue80Dark},fill:${colors.MidnightBlue80Dark},color:${colors.White},font-size:12px,line-height:20px,font-weight:400;
    classDef nonPathway font-family:roboto,stroke:${colors.GreyBase},fill:${colors.GreyBase},color:${colors.NavyBase},font-size:12px,line-height:20px,font-weight:700;
    classDef foreign_object font-family:roboto,fill:transparent,color:${colors.NavyBase},stroke-width:0,line-height:30px,font-weight:700,font-size:12px;
    classDef process fill:transparent,font-family:roboto,font-size:12px,line-height:20px,font-style:italic,stroke-width:0px,font-weight:400;

    %% Apply styles
    class PURCHASE_COMPLETE green_circle;
    
    class CUSTOMER_SELECTION,ALL_CUSTOMERS,PERC_SEE_SURVEY pathway;
    class PERC_NOT_SEE_SURVEY nonPathway;

    class ASK_NPS_SURVEY foreign_object;
    class USUAL_MENTION_ME_FLOW process;

    %% Invisible nodes
    ONWARDS1:::hidden
    ONWARDS2:::hidden
`;
};
